:root {
  --color-lightest: #fff4e6;
  --color-light: #ffe8cc;
  --color-medium: #ff944d;
  --color-dark: #ff2f2b;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

body {
  height: 100vh;
  color: #495057;
  display: flex;
  align-items: center;
  justify-content: center;
}

.app {
  min-height: 66vh;
  display: grid;
  grid-template-columns: 34rem 44rem;
  column-gap: 4rem;
  align-items: start;
}

.button {
  background-color: var(--color-medium);
  color: #343a40;
  padding: 0.8rem 1.2rem;
  border: none;
  border-radius: 7px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

.button:hover {
  background-color: var(--color-dark);
}

.sidebar ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  font-size: 1.4rem;
  margin-bottom: 2rem;
}

.sidebar li {
  display: grid;
  grid-template-columns: 4.8rem 1fr auto;
  align-items: center;
  column-gap: 1.6rem;

  padding: 1.2rem;
  border-radius: 7px;
  transition: 0.5s;
}

.selected,
.sidebar li:hover {
  background-color: var(--color-lightest);
}

.sidebar li img {
  border-radius: 50%;
  width: 100%;
  grid-row: span 2;
}

.sidebar li h3 {
  grid-row: 1;
  grid-column: 2;
}

.sidebar li p {
  grid-row: 2;
  grid-column: 2;
}

.sidebar li .button {
  grid-row: span 2;
  grid-column: 3;
}

.sidebar > .button {
  float: right;
  margin-right: 1.2rem;
}

.green {
  color: #66a80f;
}

.red {
  color: #e03131;
}

form {
  font-size: 1.6rem;
  display: grid;
  align-items: center;
  gap: 1.2rem;

  background-color: var(--color-lightest);
  border-radius: 7px;
}

.form-add-friend {
  grid-template-columns: 1fr 1.5fr;
  margin-bottom: 1.6rem;
  padding: 1.2rem;
}
.form-shopping-list {
  grid-template-columns: 1fr 12rem;
  padding: 3.2rem 4rem;
}

label {
  font-weight: 500;
}

label::first-letter {
  display: inline-block;
  margin-right: 4px;
  font-size: 1.8rem;
}

input,
select {
  font-family: inherit;
  color: inherit;
  font-size: 1.5rem;
  padding: 0.7rem;
  text-align: center;
  border: 1px solid var(--color-light);
  border-radius: 4px;
  transition: 0.3s;
}

input:focus,
select:focus {
  outline: none;
  border: 1px solid var(--color-dark);
}

form .button {
  margin-top: 0.6rem;
  grid-column: 2;
}

form h2 {
  grid-column: 1 / -1;
  font-size: 2.2rem;
  text-transform: uppercase;
  letter-spacing: -0.5px;
  margin-bottom: 1.6rem;
}

.header {
  grid-column: 1/-1;
  padding: 20px;
  margin-bottom: 10px;
  text-align: center;
  background: #ff442b;
  color: white;
  font-size: 30px;
}

ul {
  grid-row: 2;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  font-size: 1.4rem;
  margin-bottom: 2rem;
}

li {
  /* display: grid;   */
  /* grid-template-columns: 4.8rem 1fr auto; */
  align-items: center;
  column-gap: 1.6rem;
  background-color: white;

  padding: 1.2rem;
  border-radius: 7px;
  transition: 0.5s;
}

li h2 {
  grid-row: 1;
  grid-column: 2;
}
li h4 {
  grid-row: 2;
  grid-column: 2;
}
li h5 {
  grid-row: 3;
  grid-column: 2;
}

li p {
  grid-row: 2;
  grid-column: 2;
}

li .button {
  grid-row: span 2;
  grid-column: 3;
}

.add-item {
  grid-column: 1/-1;
}

li > button {
  grid-row: 3;
}

li:hover {
  background-color: #e5d868f2;
}

.shopping-list {
  display: block;
  grid-template-columns: 1fr 12rem;
  padding: 3.2rem 1rem;
  /* width: 350px; */
  height: 400px;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

.shopping-list-item {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  grid-gap: 10px;
  background-color: #ffe8cc;
  padding: 10px;
}

.shopping-list-item > .button {
  grid-row: 1;
  grid-column: 5;
}
.shopping-list-item > h2 {
  grid-row: 1;
  grid-column: 1 / 3;
}
.shopping-list-item > div {
  grid-row: 2;
  grid-column: 1 / span 5;
}
.shopping-list-item h3 {
  grid-row: 2;
  grid-column: 2 / 3;
}

.shopping-list-item div {
  grid-row: 2;
  grid-column: 3 / 5;
}

.quant input {
  max-width: 80px;
  background-color: #f9f7dd;
}
